//base
@import 'base/var';
@import 'base/reset';
@import 'base/extend';

//mixins
@import 'mixins/mixins';

//layout
@import 'layout/footer';
@import 'layout/header';
@import 'layout/card-job';

//page
@import 'page/index';
@import 'page/archive-coverage';

//google-font
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@300;400;500;700&display=swap');

//font awesome
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.2.1/css/all.min.css");


*{
    font-family: 'Noto Sans TC', sans-serif, Arial,"Font Awesome 6 Free" !important;
}

img{
    width: 100%;
}

//font style:最後有獨立一個按鈕樣式

h2, .h2{
    font-size: $h2;
    font-family: "adobe-fangsong-std", 'Noto Sans TC', sans-serif !important;
    font-weight: 600;
    font-style: normal;
    line-height: 110%;
    letter-spacing: 0.01em;
    color: $c-black;
    
    @include mini-pad{
        font-size: $m-h2;
        line-height: 102%;
    }
}
h3, .h3{
    font-size: $h3;
    font-weight: 400;
    line-height: 150%;
    letter-spacing: 0.02em;
    color: $c-black;
    
    @include mini-pad{
        font-size: $m-h3;
    }
}
h4, .h4{
    font-size: $h4;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0.02em;
    
    @include mini-pad{
        font-size: $m-h4;
    }
}
h5, .h5{
    font-size: $h5;
    font-weight: 350;
    line-height: 130%;
    letter-spacing: 0.02em;
    
    @include mini-pad{
        font-size: $m-h5;
    }
}
p, .p1{
    font-size: $p1;
    font-weight: 300;
    line-height: 120%;
    letter-spacing: 0.01em;
    color: $c-gray;
    
    @include mini-pad{
        font-size: $m-p1;
        line-height: 110%;
    }
}

.p2{
    font-size: $p2;
    font-weight: 300;
    line-height: 120%;
    letter-spacing: 0.01em;
    
    @include mini-pad{
        font-size: $m-p2;
        line-height: 105%;
    }
}

.tag{
    font-size: $tag;
    font-weight: 300;
    line-height: 120%;
    letter-spacing: 0.01em;
    
    @include mini-pad{
        font-size: $m-tag;
        line-height: 102%;
    }
}


// 按鈕樣式
.btn{
    font-size: $p2;
    font-weight: 700;
    line-height: 120%;
    letter-spacing: 0.01em;
    color:$c-white;
    background: $c-primary;

    @include mini-pad{
        font-size: $m-p2;
        line-height: 105%;
    }

    &:hover{
        background: $c-white;
        color: $c-primary;
    }

}

// 主標題
.title{
    border-bottom: 1px solid $c-secondary-light;
    padding: 68px 0px;
    text-align: center;
    @include flex($d:column);
    gap:20px;
}

//職缺區塊標籤
.job-tag{
    @include flex($w:wrap);
    gap:8px;
    >p{
        @extend %dib;
        @extend %pointer;
        color: $c-secondary;
        padding: 6px 12px;
        border: 1px solid $c-secondary;
        border-radius: 8px;
        &:hover{
            background: $c-secondary;
            color: $c-secondary-white;
        }
    }
}

// input&select - basic
input,select{
    border: none;
    &:focus{
        outline: none;
    }
}